import React, { useState, useEffect } from "react";

const Toast = ({ toastKey, theme, duration, position, remove }) => {
  const [remainingTime, setRemainingTime] = useState(duration);
  useEffect(() => {
    const timer = setInterval(() => {
      setRemainingTime((prev) => {
        const updatedTime = prev - 0.01;
        return parseFloat(updatedTime.toFixed(2));
      });
    }, 10);
      return () => {
        clearInterval(timer);
      };
  }, []);

  const styleObj = {
    dark: {
      backgroundColor: "black",
      color: "white",
    },
    light: {
      backgroundColor: "beige",
      color: "black",
    },
  };
  
  const positionObj = {
    left: {
      left: "0",
    },
    right: {
      left: "calc(100% - 250px)",
    },
    center: {
      margin: "0 auto",
    },
  };
  
  return (
    <div //   div
      key={toastKey}
      style={{
        ...styleObj[theme],
        ...positionObj[position],
        position: "relative",
        bottom: "0",
        display: "block",
        width: "250px",
        height: "60px",
        textAlign: "center",
        border: "1px solid lightblue",
        marginBottom: "5px"
      }}
    >
      <p style={{ // 
        margin: "auto", 
        lineHeight: "60px"
      }}>
        {duration}  {theme === "light" ? "" : ""}  .
      </p>

      <button //   
      style={{
        position:"absolute",
        right:"0",
        top:"0",
      }}
      onClick={() => remove()
      }></button>

      <div //    
        style={{
          width: `${(remainingTime / duration) * 100}%`,
          height: "10%",
          position: "absolute",
          bottom: "0",
          background: "green",
        }}
      >
      </div>
    </div>
  );
};

const Toaster = () => {
  const [theme, setTheme] = useState("light");
  const [position, setPosition] = useState("right");
  const [toasts, setToasts] = useState([]);

  const toastRemainingTime = 5; //   (sec)

  //  
  const handleCreateToast = () => {
    const newToast = {
      id: Date.now(),
      theme: theme,
      position: position,
    };
    setToasts((prev) => [...prev, newToast]);
    setTimeout(() => {
      removeToast(newToast.id);
    }, toastRemainingTime * 1000);
  };
  
  //  
  const removeToast = (toastId) => {
    setToasts((prev) => prev.filter((toast) => toast.id !== toastId));
  };

  return (
    <div style={{ position: "relative" }}>
      <h1>Toast</h1>
      <button onClick={() => setTheme("dark")}> </button>
      <button onClick={() => setTheme("light")}> </button>
      <br />
      <button onClick={() => setPosition("left")}></button>
      <button onClick={() => setPosition("center")}></button>
      <button onClick={() => setPosition("right")}></button>
      <br />
      <button onClick={handleCreateToast}> </button>
      <div
        style={{
          zIndex: "1",
          width: "100%",
          position: "fixed",
          bottom: "0",
        }}
      >
        {toasts.map((toast) => (
          <Toast
            key={toast.id}
            theme={toast.theme}
            duration={toastRemainingTime}
            position={toast.position}
            remove={() => removeToast(toast.id)}
          />
        ))}
      </div>
    </div>
  );
};

export default Toaster;