import { getCart } from "../../apis/api";
import { useQuery } from "react-query";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import GNB from "./GNB";
import CartLIst from "../organisms/CartList";

const CartTemplate = () => {
  const { data : cart } = useQuery('cart', getCart, {suspense: true}); // api    
  const [totalPrice, setTotalPrice] = useState(cart?.data.response.totalPrice);

  useEffect(() => {
    setTotalPrice(cart.data.response.totalPrice);
  }, [cart])

  const navigate = useNavigate();

  const handleBuyClick = () => {
    navigate('/order');
  }

  return (
    <>
      <GNB></GNB>
      <div className="py-5 border font-bold text-center"></div>
      <CartLIst cart={cart} setTotalPrice={setTotalPrice}></CartLIst>
      <div className="fixed bottom-0 w-full border bg-white">
        <div className="p-5 flex justify-between font-bold">
          <span>  </span> 
          <span className="text-blue-700">{totalPrice} </span>
        </div>
        <button onClick={handleBuyClick} className="w-full text-center bg-yellow-300 py-3 font-bold">  </button>
      </div>
    </>
  )
}
export default CartTemplate;