import { useState } from 'react';
let counter = 1;
const initialRadioButtons = [
  {value: "", index: counter++},
  {value: "", index: counter++},
  {value: "", index: counter++},
]
function RadioButton() {
  const [selectedOption, setSelectedOption] = useState('');
  const [selectedIndex, setSelectedIndex] = useState(0); // value     index  
  const [radioButtons, setRadioButtons] = useState(initialRadioButtons);
  const [inputValue, setInputValue] = useState('');

  const handleOptionChange = (event, index) => {
    setSelectedOption(event.target.value);
    setSelectedIndex(index);
  };
  
  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const addNewButton = () => {
    if (inputValue !== '') {
      const newButton = { value: inputValue, index: counter++ };
      setRadioButtons([...radioButtons, newButton]);
      setInputValue('');
    }
  };

  return (
    <div style={{marginTop:"20px"}}>
      <h1>Radio</h1>
      {radioButtons.map((radioButton) => (
          <label key={radioButton.index}>
            <input 
              type="radio"
              value={radioButton.value}
              checked={selectedIndex === radioButton.index}
              onChange={(event) => handleOptionChange(event, radioButton.index)}/>
            {radioButton.value}
          </label>
      ))}
      <div>
      {selectedOption}() .
      </div>
      <div>
      <input
        type="text"
        value={inputValue}
        onChange={handleInputChange}
        />
      <button onClick={addNewButton}>  </button>
      </div>
    </div>
  );
}

export default RadioButton;