const Input = ({
  type,        //   
  value,       //   
  onChange,    //     
  placeholder, // placeholder
  className,   //  
  id,          // input  id , label  id
  autocomplete,// 
}) => {
  return <input
            type={type}
            id={id}
            className={className} 
            value={value} 
            onChange={onChange} 
            placeholder={placeholder}
            autoComplete={autocomplete}
             />;
}
export default Input;